#grid {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    margin-top: 5px;
  }
  
  table, tr, td {
    border: 1px solid black; 
  }
  
  td {
    width: 70px;
    height: 70px;
    text-align: center;
    vertical-align: middle;
    font: 2em 'Fredoka One', cursive;
  }
  
  h2 {
    color: blue;
    margin: 10px 0 7px 0;
  }